import axios  from "axios";
async function submitData(name,email, mobile , college , department, sem ,preferred_location) { 
    try{
        const response = await axios.post('/insert',
         {name, email, mobile, department ,sem ,college , preferred_location});
        return  response.data;
    }
    catch(error){
        new Error(error)
    }
}

export default submitData ;